import colors from '../colors'
import posed from "react-pose";

const Square = posed.div({
  idle: { scale: 1 },
  hovered: { scale: 1.05 }
});

class Member extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      h: false,
    }
  }

  render() {
    return(
      <div className="is-flex" style={{flexDirection: "column", alignItems: "center"}}>
        <Square
          pose={this.state.hTeam ? "hovered" : "idle"}
          onMouseEnter={() => this.setState({ h: true })}
          onMouseLeave={() => this.setState({ h: false })}>
          {(this.state.h ? (
            <div className="has-text-white has-text-centered is-flex" style={{flexDirection: "column", justifyContent: "center", height: 200, width: 200, borderRadius: 100, backgroundColor: colors.b4, padding: 10}}>
              <p className="is-size-5"> <b> Full Name</b> </p>
              <p className="is-size-6"> Grade </p>
              <p className="is-size-6"> Hometown </p>
              <p className="is-size-6"> Major </p>
            </div>) : (
            <div>
              <div style={{height: 200, width: 200, borderRadius: 100, backgroundColor: colors.b3, padding: 10}}>
              </div>
              <p className="is-size-5" style={{color: colors.g1}}> <b> First Name </b> </p>
              <p className="is-size-6" style={{color: colors.g1}}> <em> Role </em> </p>
            </div>
          ))}
        </Square>
      </div>
    );
  }
}

export default Member;
